body {
  font-family: cursive;
  box-sizing: border-box;
  background-color:  rgb(4, 4, 88);
  font-size: 1.5 rem;
}

.container {
  float: left;
  margin-top: 3rem;
  margin-left: 0.5rem;
}

.card-body {
  padding: 6rem;
  margin-top: 1rem;
  background-color: whitesmoke;
  margin-bottom: 4rem;
  margin-left: 5rem;
}

.h5,
h5 {
  font-size: 2.5rem;
}

.card-title {
  margin-bottom: 0.85rem;
  color: #343a40;
}

img {
  vertical-align: middle;

  margin-bottom: 7px;
  max-width: 80%;
  padding: 1rem;
}

.card {
  border: 0px;
  display: inline;
}

p {
  margin-top: 1rem;
}

footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #343a40;
  color: white;
  text-align: center;
}

a {
  font-size: 1.4rem;
  color:rosybrown;
  text-decoration: none;
  margin-right: 1rem;
  position: right;
  
}

.navbar-dark .navbar-brand {
  font-size: 1.5rem;
}

a {
  font-size: 1.25rem;
}